<template>
	<view class="container" v-if="schoolData.titlepic">
		<view class="header">
			<view class="top">
				<image :src="schoolData.titlepic" mode="" class="logo"></image>
				<view class="name">
					<view class="title">{{schoolData.title}}</view>
					<view class="tags">
						<view class="item" v-for="item in tags" :key="item">{{item}}</view>
					</view>
				</view>
			</view>
			
		</view>
		<view class="info unifycase">
			<view class="tit">
				基本信息
			</view>
			<view class="box">
				<view class="item">
					举办者：{{schoolData.jubanzhe}}
				</view>
				<view class="item">
					建校时间：{{schoolData.jianxiaoshijian}}
				</view>
				<view class="item">
					招生代码：{{schoolData.zhaoshengdaima}}
				</view>
				<view class="item">
					在校生人数：{{schoolData.xueshengrenshu}}
				</view>
				<view class="item">
					学校面积：{{schoolData.xuexiaomianji}}
				</view>
				<view class="item">
					教学设备价值：{{schoolData.shebeijiazhi}}
				</view>
				<view class="item">
					教职工总数：{{schoolData.xueshengrenshu}}
				</view>
				<view class="item">
					挂靠大专学校：{{schoolData.guahaodazhuan}}
				</view>
				<view class="item">
					学校地址：{{schoolData.xuexiaodizhi}}
				</view>
				<view class="item">
					学校官网：{{schoolData.xuexiaogw}}
				</view>
			</view>
		</view>
		<view class="content unifycase">
			<view class="tit">
				学校介绍及优势
			</view>
			<view class="box">
				<mp-html ref="content" domain="https://www.shangzhixiao.com" :content="contentData" />
			</view>
		</view>
		<view class="environment unifycase">
			<view class="tit">
				学校环境
			</view>
			<view class="box" if="environmentData">
				<mp-html @linktap="linktap" ref="environment" domain="https://www.shangzhixiao.com" :content="environmentData"  />
			</view>
		</view>
		<DetailsSuspend :schoolName="schoolData.title"/>
	</view>
</template>

<script>
	import {
		getSchoolDetails
	} from '@/api/school.js'
	import DetailsSuspend from '@/components/library/DetailsSuspend.vue'
import { websiteUrl } from '@/utils/config.js'
	export default {
		data() {
			return {
				id: null,
				schoolData: {},
				environmentData: '',
				contentData: '',
				tags: [],
			};
		},
		components: {
			DetailsSuspend
		},
		onLoad(e) {
			this.id = e.id
			getSchoolDetails(this.id).then(({
				data
			}) => {
				data.titlepic = websiteUrl + data.titlepic
				this.contentData = data.newstext.replace(/[\\]/g, '')
				this.environmentData = data.huanjing.replace(/[\\]/g, '')
				this.schoolData = data
				this.tags = [...data.xuexiaoxingzhi.split('|'), ...data.pttags.split('|'), data.xuexiaoleixing]
					.filter(item => item.length > 0)
				uni.setNavigationBarTitle({
					title: data.title
				})
			})
			
		},
		// 点击分享按钮
		
		onShareAppMessage(res) {
			if (res.from === 'botton') {
				console.log(res.target);
			}
			let pages = getCurrentPages() // 获取栈实例
			let currentRoute  = pages[pages.length-1].route; // 获取当前页面路由
			let currentPage = pages[pages.length-1]['$page']['fullPath']
			return {
				title: this.schoolData.title,
				path: currentPage,
				imageUrl: this.schoolData.titlepic,
				content: this.schoolData.smalltext,
				desc: this.schoolData.smalltext
			}
		}
	}
</script>

<style lang="scss">
	page {
			// background-color: #fff;
		}
	.container {
		padding-bottom: 40px;
		.header {
			margin: 10px 8px 0px 8px;
			padding: 10px;
			background: #00c6ff;  /* fallback for old browsers */
			background: -webkit-linear-gradient(to right, #0072ff, #00c6ff);  /* Chrome 10-25, Safari 5.1-6 */
			background: linear-gradient(to right, #0072ff, #00c6ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

			border-radius: 10px;

			.top {
				display: flex;
			}

			.logo {
				flex-shrink: 0;
				margin-right: 8px;
				width: 80px;
				height: 80px;
			}
			.name{
				display: flex;
				flex-direction: column;
				justify-content: center
			}
			.title {
				font-size: 18px;
				color: #fff;
				font-weight: 700;
			}

			.tags {
				margin-top: 8px;
				display: flex;
				flex-wrap: wrap;
				& .item {
					display: inline-block;
					border-radius: 20px;
					font-size: 12px;
					padding: 0 8px;
					color: #fff;
					border: 1px solid #fff;
					margin-right: 8px;
				}
			}
		}
	}

	.unifycase {
		margin-top: 20px;
		padding: 25px 10px;
		background-color: #fff;
		border-radius: 10px;
	}

	.info {
		.box {
			padding-left: 15px;
		}

		.item {
			font-size: 16px;
			margin-top: 6px;
			color: #000;
		}
	}

	.content {
		font-size: 15px;
		line-height: 25px;
		text-align: justify;
		letter-spacing: 0.8px;
	}

	.environment {
		background-color: #fff;
		margin-top: 20px;
		padding: 25px 0;

		.tit {
			padding-left: 8px;
		}
	}

	.tit {
		display: flex;
		align-items: center;
		font-weight: 700;
		height: 18px;
		font-size: 18px;
		margin-bottom: 10px;

		&:before {
			display: inline-block;
			content: "";
			width: 2px;
			height: 100%;
			margin-right: 8px;
			background-color: #0092ff;
		}
	}
</style>
